// The Vue build version to load with the `import` command
/*              webpack.base.conf    */
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import { store } from './store/store.js'
import VueGlide from 'vue-glide-js'
import 'vue-glide-js/dist/vue-glide.css'
import axios from 'axios'
import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import 'tui-grid/dist/tui-grid.css'
import 'tui-date-picker/dist/tui-date-picker.css'
import 'tui-time-picker/dist/tui-time-picker.css'
import 'tui-pagination/dist/tui-pagination.css'
import { Grid } from '@toast-ui/vue-grid'
import TuiGrid from 'tui-grid'
import { commonUtil } from '@/jsUtil/commonUtil.js'
/* Vue.component       main.js import */
import '@/css/fontawesome.js'

/*   */
Vue.config.productionTip = false

TuiGrid.setLanguage('ko')
TuiGrid.applyTheme('clean')
Vue.component('grid', Grid)

/*   */
Vue.use(VueGlide)
Vue.use(BootstrapVue)

Vue.prototype.$commonJs = commonUtil
Vue.prototype.$http = axios

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store: store,
  router,
  components: { App },
  template: '<App/>'
})

router.beforeEach((to, from, next) => {
  let user = JSON.parse(localStorage.getItem('vuex'))
  if (to.path.indexOf('admin') > 0) {
    if (user.userStore.userInfo.user_auth === 'USER') {
      next()
    } else {
      alert(' .')
    }
  } else {
    next()
  }
})



